@font-face {
  font-family: "PlayfairDisplay-Black";
  src: url("/fonts/Playfairdisplayblack.eot"); /* IE9 Compat Modes */
  src: url("/fonts/Playfairdisplayblack.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("/fonts/Playfairdisplayblack.otf") format("opentype"), /* Open Type Font */
    url("/fonts/Playfairdisplayblack.svg") format("svg"), /* Legacy iOS */
    url("/fonts/Playfairdisplayblack.ttf") format("truetype"), /* Safari, Android, iOS */
    url("/fonts/Playfairdisplayblack.woff") format("woff"), /* Modern Browsers */
    url("/fonts/Playfairdisplayblack.woff2") format("woff2"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

